@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;font-family:"Red Hat Display",sans-serif}.container{width:100vw;height:100vh;background-color:#e0e8ff;background-image:url(/order-summary-component/static/media/pattern-background-desktop.5a0ab689.svg);background-repeat:no-repeat;background-size:100vw}.content{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.card{width:350px;border-radius:15px;background-color:#f5f7ff}.card__content{padding:35px}.card__head>img{width:350px;border-top-left-radius:15px;border-top-right-radius:15px}.card__item{display:flex;align-items:center;justify-content:space-between;margin-top:10px;margin-bottom:10px;padding-left:10px;padding-right:10px;padding-top:10px;padding-bottom:10px;background-color:rgba(224,232,255,0.3);border-radius:10px}.title{font-weight:bold;font-size:13px}.price{font-size:13px}.card__icon{width:25%}.card__text{display:flex;flex-direction:column;justify-content:center;width:50%}.card__btns{width:25%}.card__footer{display:flex;flex-direction:column}h2,p{text-align:center;margin-bottom:15px}p{color:#7280a7;font-size:16px}button{cursor:pointer;background-color:transparent;border:none;margin-top:10px}.btn{color:#e0e8ff;border-radius:10px;padding:15px;font-weight:bold}.btn.btn__primary{background-color:#3829e0;box-shadow:0px 29px 19px 0px rgba(0,0,0,0.13)}.btn.btn__primary:hover{opacity:0.5}.btn__secondary{color:#7280a7}.btn__secondary:hover{color:#000}.btn__link{color:#3829e0;font-weight:bold;-webkit-text-decoration-line:underline;text-decoration-line:underline;margin-top:0}.btn__link:hover{opacity:0.5;-webkit-text-decoration-line:none;text-decoration-line:none}.card__text>p{text-align:start;margin-bottom:0}@media (min-width: 320px) and (max-width: 992px){.container{background-image:url(/order-summary-component/static/media/pattern-background-mobile.c8d50fcb.svg)}.card,.card__head>img{width:325px}}

